.form {
    width: 30%;
    padding-top: 15vh;
    margin-left: 7vw;
    box-sizing: border-box;
 
}

.formHeader {
    background-color: #F00000;
    color: white;
    text-align: center;
    padding: 18px 30px;
    background: #F00000;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.36);
    border-radius: 5px;
}

.formBody {
    display: flex;
    flex-direction: column;
    gap: 4px;
   
    padding: 10px 10px 50px;
    background-color: white;
    margin-top: 20px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.36);
    border-radius: 5px;
}

.formBody input {
    padding: 12px 20px;
    margin: 10px;
}

.formSelect {
    padding: 12px 20px;
    margin: 10px;
 
}

.formButton {
    margin: 25px 10px 20px;
    background: #B5B5B5;
    box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.46);
    border-radius: 2px;
    padding: 10px;
    border: none;
    color: white;
    font-weight: 700;
}

.formButtonActive {
    margin: 10px;
    background: #37CC8A;
    box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.46);
    border-radius: 2px;
    padding: 10px;
    border: none;
    color: white;
    font-weight: 700;
    cursor: pointer;
}

.skills {
    margin: 0 10px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.skillTag {
    display: flex;
    gap: 5px;
    font-weight: 500;
    font-size: 1rem;
    line-height: 99.19%;
    letter-spacing: 0.01em;
    color: #777777;
    padding: 2px 10px;
    background: #DCDCDC;
    border-radius: 8px;
}

.skillTag span {
    cursor: pointer;
}

.disclaimer {
    font-weight: 500;
    font-size: 13px;
    padding-top: 5px;
    text-align: center;
    color: #C2C2C2;
    ;
}